.c-data-options__spacer {
    margin: 20px 0;
  }

.data-options-component-grouping {
    margin-top: 30px;
    margin-bottom: 10px;
    margin-left: 30px;
  }

.data-options-component-mapping-title {
    margin-top: 30px;
  }

@media screen and (min-width: 768px) {

.data-options-component-expansion-content {
      padding-left: 30px
  }
    }
.data-options-component-page .pf-c-expandable-section {
    padding-bottom: 30px;
  }
.data-options-component-page .pf-c-expandable-section__toggle {
      justify-content: flex-start;
      border-bottom: var(--pf-global--BorderWidth--sm) solid #e6e6e6 !important;
      width: 100%;
    }
.data-options-component-page .pf-c-expandable-section__toggle-text {
        font-size: var(--pf-global--FontSize--xl);
        font-weight: var(--pf-global--FontWeight--bold);
        color: var(--pf-global--Color--100);
      }
@media screen and (max-width: 768px) {
.data-options-component-page .pf-c-switch,
  .data-options-component-page .pf-c-switch__label {
      display: inline
  }
    }
