.create-connector-page_breadcrumb {
    padding: 0px !important;
    border-bottom: 1px solid var(--pf-global--BorderColor--100);
  }
    .create-connector-page_breadcrumb .pf-l-level .pf-c-content {
        padding: 15px 0px;
      }
  .create-connector-page_wizard {
    height: auto;
    min-height: calc(100vh - 200px);
  }
  .create-connector-page_wizard .pf-c-wizard__nav-link.pf-m-current {
          color: var(--pf-c-wizard__nav-link--m-current--Color);
        }
  .create-connector-page_wizard .pf-c-wizard__nav-list {
        margin: 24px 5px;
        padding: 0 10px 0 30px;
      }
  .create-connector-page_wizard .pf-c-wizard__nav-item {
        margin-bottom: 30px;
      }
  .create-connector-page_wizard .pf-c-wizard__nav-item ol {
          padding: 0 10px;
        }
  .create-connector-page_wizard .pf-c-wizard__nav-item ol li {
            margin-bottom: 20px;
          }
  .create-connector-page_wizard .pf-c-wizard__nav-item .pf-m-disabled:before {
            background-color: var(--pf-global--disabled-color--300);
          }
  .create-connector-page_wizard .pf-c-wizard__nav-item .pf-m-required {
          color: var(--pf-global--palette--red-100);
        }

.skipToNextInfoBox {
  width: 100%;
  margin-bottom: 40px;
}

.skipToNextInfoBox .pf-c-button.pf-m-link {
    padding-left: 5px;
    padding-right: 5px;
  }

.skipToNextInfoBox .titleSuccess {
    color: var(--pf-global--palette--green-500);
  }

.skipToNextInfoBox .titleInfo {
    color: var(--pf-global--info-color--100);
  }
.pf-c-notification-drawer {
  box-shadow: var(--pf-global--BoxShadow--sm);
}
