.connectors-page_createButton {
    margin-top: var(--pf-c-empty-state__primary--MarginTop);
  }
  .connectors-page_toolbarCreateButton {
    margin-bottom: var(--pf-global--spacer--sm);
  }
  .connectors-page_toolbarSortIcon {
    cursor: pointer;
  }
  .connectors-page_dataTable {
    margin-top: var(--pf-global--spacer--sm);
  }
  @media screen and (max-width: 768px) {
  .connectors-page_dataTable tr:nth-child(2) {
        padding: 20px
    }
      }
  @media screen and (max-width: 768px) {
  .connectors-page_dataTable tr:nth-child(2) td:nth-child(2),
      .connectors-page_dataTable tr:nth-child(2) td:nth-child(3) {
          display: none
      }
        }
  .connectors-page_dataTable tr:nth-child(2) td:nth-child(6) .pf-c-dropdown {
          display: none;
        }
  .connectors-page_dataTable tr:nth-child(2) td:nth-child(4) div.pf-l-flex {
          padding: 8px;
        }
  .connectors-page_toolbarFlex {
    padding: var(--pf-global--spacer--sm) 0;
    border-bottom: 1px solid var(--pf-global--palette--black-200);
  }
  .connectors-page_toolbarFlex .flexCol {
      flex-direction: column;
    }
  .connectors-page_toolbarFlex div:nth-child(2) {
      margin-right: var(--pf-global--spacer--xl);
    }
  .connectors-page_toolbarFlex .pf-c-button.pf-m-primary {
        margin-bottom: 0;
      }
  .connectors-page_toolbarFlex .pf-c-toolbar__content {
        padding: 0;
      }

.status-indicator {
  height: 25px;
  border-radius: 50%;
  margin-right: 10px;
  text-align: center;
  color: white;
  display: inline-block;
}

.pf-c-table tbody > tr > * {
        vertical-align: middle;
        padding: 15px var(--pf-c-table--cell--PaddingRight) 10px
          var(--pf-c-table--cell--PaddingLeft);
      }

.pf-c-table .pf-c-label.no-bg {
      background: none;
    }
.pf-c-tooltip .errors {
    text-align: left;
  }
