.connector-type-step-component_flex {
    flex-wrap: wrap;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
  }
    .connector-type-step-component_flex > div {
      text-align: center;
      cursor: pointer;
      flex-grow: 1;
      margin: 10px;
    }
    .connector-type-step-component_flex_disableCard {
      cursor: default;
      color: var(--pf-global--disabled-color--100);
      background: var(--pf-global--palette--black-150);
    }
    .connector-type-step-component_flex .pf-c-card {
      min-height: 220px;
    }
    .connector-type-step-component_flex .pf-c-card__header {
        height: 95px;
        flex-direction: column;
      }
  .connector-type-step-component_dbIcon {
    width: 50%;
    margin: 0 auto;
  }
  .connector-type-step-component_dbIcon > svg {
      font-size: xx-large;
    }
  .connector-type-step-component_cardAction {
    font-size: var(--pf-global--FontSize--sm);
    background-color: #fdf7e6;
    padding: 0 5px;
    margin-bottom: 10px;
    line-height: 1.2;
    order: 0;
  }
  .connector-type-step-component_cardAction--icon {
      padding-right: 3px;
      color: #ee7d05;
    }
